<template>
  <div class="propertyOrderPayDetail" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth">
      <template v-slot:nav-right>
        <div class="rightBtn" @click="callProperty" v-if="isDisplayRightName">{{rightName}}</div>
      </template>
    </navigation-bar>
    <div class="propertyOrderPayDetailContent">
      <div class="propertyOrderPayDetailCard">
        <div class="project-item-content">
          <div class="pre-img" @click="preItemContent">
            <img :src="swiperItemIndex != '0' ? preRedImg : preGrayImg" alt />
          </div>
          <div class="swipe-view">
            <swiper ref="projectItemSwipe"  :options="swiperOption">
              <swiper-slide v-for="(room,roomIndex) in orderDetail.roomList" :key="roomIndex">
                <div class="swiper-item-content">
                  <div class="user_info">
                    <img class="user_img" :src="houseImg" />

                    <div class="user_word">
                      <div class="user_home">{{room.room_name}}</div>
                      <div class="user_name">房主：{{room.owner_name}}</div>
                    </div>
                  </div>

                  <div style="display: flex;height: 20px;top:174px;width: 100%;">
                    <div class="circleLeft"></div>
                    <div class="lineStyle" style="position:relative;left:5px;top:6px;"></div>
                    <div class="circleRight"></div>
                  </div>
                  <div class="order_itme_list">
                    <div v-for="(x,index) in room.monthList" :key="index" style="margin-bottom: 7px;">
                      <div
                        class="item-content"
                        @click="showTime(roomIndex,index)"
                      >
                        <div
                          style="width: 50%;padding-left: 5%;font-size:14px;"
                          :style="{fontSize:itemFont}"
                        >{{x.month_name}}</div>
                        <div
                          style="width: 40%;text-align: right;font-size:14px;"
                          :style="{fontSize:itemFont}"
                        >￥{{x.month_amount| keepTwoNum}}</div>
                        <img
                          width="14"
                          height="14"
                          style="margin-top: 21px"
                          :src="x.isShow?arrowDownImg:arrowRightImg"
                        />
                      </div>
                      <div v-if="x.isShow">
                        <div
                          class="freeShow"
                          v-for="(item,monthIndex) in x.billDetailList"
                          :key="monthIndex"
                        >
                          <div class="freeText">{{item.payment_type}}</div>
                          <div class="freeText">￥{{item.payment_amount| keepTwoNum}}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </swiper-slide>
            </swiper>
          </div>

          <div class="next-img" @click="nextItemContent">
            <img :src="orderDetail.roomList && swiperItemIndex != orderDetail.roomList.length - 1 + '' ? nextRedImg : nextGrayImg" alt />
          </div>
        </div>
        <div style="display: flex;position: relative;height: 20px;width: 100%;">
          <div class="circleLeft"></div>
          <div class="lineStyle"></div>
          <div class="circleRight"></div>
        </div>
        <div class="navContent">
          <div class="nav">
            <div class="orderDiv">
              <div class="orderDetail">
                <div style="font-size: 12px;" :class="{orderDivSonThemeL:bindThemeL}">
                  <span class="orderDivSon">订单编号</span>
                  <input
                    type="text"
                    name="selectCopy"
                    id="selectCopy"
                    class="orderNumber"
                    v-model="billNo"
                    style="position: absolute;left:-100%;bottom:-5%"
                    readonly
                  />
                  {{billNo}}
                </div>
                <button class="orderCopy" @click="copy()">复制</button>
              </div>
              <div class="orderDivSon" v-if="orderDetail.bill_type=='01'">
                订单类型
                <span class="orderSpan">账单缴费</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.pay_type==1">
                支付方式
                <span class="orderSpan">微信</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.pay_type==2">
                支付方式
                <span class="orderSpan">支付宝</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.payTime">
                支付时间
                <span class="orderSpan">{{orderDetail.payTime}}</span>
              </div>
              <div class="orderDivSon">
                创建时间
                <span class="orderSpan">{{orderDetail.createTime}}</span>
              </div>
              <div class="orderDivSon" v-if="orderDetail.obtainable_piont">
                可获积分
                <span class="orderSpan">{{orderDetail.obtainable_piont}}</span>
              </div>
            </div>
          </div>
          <div class="line"></div>
          <div class="total_and_deduction">
            <div class="total_money">
              <div class="total_name">订单总额</div>
              <div class="total_num">￥{{orderDetail.bill_amount| keepTwoNum}}</div>
            </div>
            <div class="deduction_point">
              <div class="deduction_name">积分抵扣</div>
              <div class="deduction_num">-￥{{orderDetail.point_deduction/100 | keepTwoNum}}</div>
            </div>
          </div>
          <div class="line"></div>
          <div class="payBottom">
            <div class="payBottomStatueBlue" v-if="orderDetail.bill_status==1">已支付</div>
            <div class="payBottomStatueRed" v-if="orderDetail.bill_status==0">待支付</div>
            <div class="payBottomStatueBlack" v-if="orderDetail.bill_status==2">已取消</div>
            <div class="payBottomText">
              <span class="payBottomSpan">实付：</span>
              <span
                class="payBottomMoney"
                v-if="orderDetail.bill_status==1"
              >￥{{orderDetail.realPayAmount| keepTwoNum}}</span>
              <span class="payBottomMoney" v-if="orderDetail.bill_status==0">￥{{realPay}}</span>
              <span class="payBottomMoney" v-if="orderDetail.bill_status==2">￥{{realPay}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style="height:87px;" v-if="orderDetail.bill_status==0"></div>
    <div class="order_pay" v-if="orderDetail.bill_status==0">
      <div class="order_num" @click="cancelOrder()">取消订单</div>
      <div class="soon_pay" @click="gotopay()">去支付</div>
    </div>
    <property-pay-popup
      v-if="payWindowShow"
      :payParam="payParam"
      :isAlipay="isAlipay"
      :isWechart="isWechart"
      @closePopup="closePopup"
      @checkOrderStatus="checkOrderStatus"
    ></property-pay-popup>
  </div>
</template>

<script>
import {
  queryOrderDetailService,
  payOrderService,
  queryPaymentDetailService,
  orderCancelService
} from "../../../services/propertyBillService";
import propertyOrderGotoPay from "./propertyOrderGotoPay";
import propertyPayPopup from "./propertyPayPopup";
import NavigationBar from "@/common/currency/NavigationBar.vue";
//swiper组件引入
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
export default {
  components: {
    swiper,
    swiperSlide,
    NavigationBar,
    "property-order-goto-pay": propertyOrderGotoPay,
    "property-pay-popup": propertyPayPopup
  },
  name: "propertyOrderPayDetail",
  data() {
    const self = this
    return {
      billNo: "", //订单编号
      areaCode: "",
      bill_amount: 0, //订单金额
      billCodePoint: 0, //积分抵扣后的订单金额
      pointAviablePersent: 0, //积分抵扣
      pointAviable: 0, //可用积分
      integralAll: false, //是否使用全部积分
      integralDeduction: 0, //使用的积分值
      realPay: 0, //实付金额
      orderDetail: {},
      payWindowShow: false, //支付弹窗
      isAlipay: false,
      isWechart: false,
      headerItemWidth: {
        width: "110px"
      },
      title: "订单支付",
      rightName: "联系物业",
      isDisplayRightName: false,
      switchOnImg: require("../../../assets/images/home/property/payment/switch_on.png"),
      switchOffImg: require("../../../assets/images/home/property/payment/switch_off.png"),
      houseImg: require("../../../assets/images/home/property/payment/house_house.png"),
      addImg: require("../../../assets/images/home/property/payment/bg_add.png"),
      cutImg: require("../../../assets/images/home/property/payment/bg_cut.png"),
      tipsImg: require("../../../assets/images/home/property/payment/icon_tips.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      arrowDownImg: require("../../../assets/images/home/property/payment/icon_arrow_down_black.png"),
      nextRedImg: require("../../../assets/images/home/property/payment/arrow_next_red.png"),
      preRedImg: require("../../../assets/images/home/property/payment/arrow_pre_red.png"),
      nextGrayImg: require("../../../assets/images/home/property/payment/arrow_next_gray.png"),
      preGrayImg: require("../../../assets/images/home/property/payment/arrow_pre_gray.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme == 0 ? false : true,
      itemFont: this.$curTheme == 0 ? "14px" : "17px",

      
      swiperItemIndex: '0',
      swiperOption: {
        //首个居中
        centeredSlides: true,
        // 自动高度，让 siwper 的高度跟随 slide 的高度变化
        autoHeight: true,
        // 启动动态检查器
        observer: true,
        // 启动父类动态检查器
        observeParents: true,
        // 无限循环模式
        loop: false,
        on: {
          slideChangeTransitionEnd: function() {
            self.swiperItemIndex = this.realIndex;
            console.log('===>>>>>>> 当前index： ' + self.swiperItemIndex);
          }
        },
      }
    };
  },
  mounted() {
    this.billNo = this.$route.params.billCode;
    this.areaCode = this.$route.params.areaCode;
    this.queryOrderDetail();
  },
  methods: {
    preItemContent() {
      this.$refs.projectItemSwipe.swiper.slidePrev();
    },
    nextItemContent() {
      this.$refs.projectItemSwipe.swiper.slideNext();
    },
    queryOrderDetail() {
      var reqData = {
        bill_code: this.billNo
      };
      queryOrderDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            for (let index in result.data.roomList) {
              for (let monthIndex in result.data.roomList[index].monthList) {
                result.data.roomList[index].monthList[
                  monthIndex
                ].isShow = false;
              }
            }
            this.orderDetail = result.data;
            //add for ZHSQYYPT-8358
            this.isDisplayRightName =
              this.orderDetail.propertyPhoneNumber != "物业电话尚未配置";

            this.bill_amount = result.data.bill_amount;
            this.billCodePoint = result.data.bill_amount;
            this.realPay = parseFloat(
              this.orderDetail.bill_amount -
                this.orderDetail.point_deduction / 100
            ).toFixed(2);
            console.log(JSON.stringify(this.orderDetail));
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 查询物业费明细 展开内容 */
    queryBillDetail(parentIndex, index) {
      var reqData = {
        bill_no: this.billNo,
        room_code: this.orderDetail.roomList[parentIndex].room_code,
        month_code: this.orderDetail.roomList[parentIndex].monthList[index]
          .month_code
      };
      queryPaymentDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result.result) {
            if (result.data.billDetailList) {
              this.orderDetail.roomList[parentIndex].monthList[
                index
              ].billDetailList = result.data.billDetailList;

              this.orderDetail.roomList[parentIndex].monthList[
                index
              ].isShow = !this.orderDetail.roomList[parentIndex].monthList[
                index
              ].isShow;
            } else {
              this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 获取某月账单物业费明细 */
    showTime(parentIndex, index) {
      //判断月份详情是否存在，不存在进行网络请求
      if (
        this.orderDetail.roomList[parentIndex].monthList[index]
          .billDetailList == null ||
        this.orderDetail.roomList[parentIndex].monthList[index]
          .billDetailList == "undefined"
      ) {
        this.queryBillDetail(parentIndex, index);
      } else {
        this.orderDetail.roomList[parentIndex].monthList[index].isShow = !this
          .orderDetail.roomList[parentIndex].monthList[index].isShow;
      }
    },
    cancelOrder() {
      Dialog.confirm({
        title: "提示",
        message: "确认要取消订单吗？"
      })
        .then(() => {
          console.log("取消确认回调");
          var reqData = {
            bill_code: this.billNo
          };
          orderCancelService({ data: JSON.stringify(reqData) })
            .then(result => {
              //请求成功
              if (result.result) {
                this.$toast({
                  duration: 500,
                  message: "取消成功",
                  onClose: () => {
                    this.queryOrderDetail();
                  }
                });
              } else {
                this.$toast(result.msg ? result.msg : "取消失败，请稍后重试！");
              }
            })
            .catch(error => {
              //加载失败后，关闭加载等待的文字
              this.$toast("网络异常，请稍后重试！");
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    onClickLeft() {
      this.$router.back();
    },
    /* 立即支付 */
    gotopay() {
      let reqData = {
        bill_no: this.billNo,
        point: this.orderDetail.point_deduction + "",
        decount_money: parseFloat(
          this.orderDetail.point_deduction / 100
        ).toFixed(2),
        pay_money: parseFloat(
          this.orderDetail.bill_amount - this.orderDetail.point_deduction / 100
        ).toFixed(2)
      };
      console.log(JSON.stringify(reqData));
      payOrderService({ data: JSON.stringify(reqData) })
        .then(result => {
          if (result.result == true) {
            /* 获取支付参数 */
            this.payParam = result.payParam;
            /* 判断支持的支付方式 */
            if (result.payParam.IS_ALI_PAY == 1) {
              this.isAlipay = true;
            }
            if (result.payParam.IS_WX_PAY == 1) {
              this.isWechart = true;
            }
            /* 支付弹窗 */
            this.payWindowShow = true;
            // /*监听ios支付结果 检测当前订单状态*/
            window.broadcaster.addEventListener(
              "paymentBack",
              this.payCallBack
            );
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    payCallBack() {
      this.checkOrderStatus();
    },
    checkOrderStatus() {
      /* 取消监听，避免重复监听 */
      window.broadcaster.removeEventListener("paymentBack", this.payCallBack);
      /* 获取订单数据 */
      let reqData = { bill_code: this.billNo };
      queryOrderDetailService({ data: JSON.stringify(reqData) })
        .then(result => {
          if (result.result) {
            if (result.data.bill_status == 1) {
              //订单状态为已支付，跳转支付成功界面
              result.data.billType = "账单缴费";
              this.$router.push({
                name: "paymentPaySuccess",
                params: { orderData: result.data }
              });
            } else {
              //支付失败，返回列表页
              this.$toast({
                duration: 500,
                message: "支付失败",
                onClose: () => {
                  this.$router.back();
                }
              });
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    closePopup() {
      this.payWindowShow = false;
    },
    /**
     * 复制订单号
     */
    copy() {
      cordova.plugins.clipboard.copy(
        this.billNo,
        () => {
          this.$toast("复制成功");
        },
        function() {
          console.log("error");
        }
      );
    },
    callProperty() {
      if (device.platform == "Android") {
        Dialog.confirm({
          title: "拨打电话",
          message: this.orderDetail.propertyPhoneNumber
        }).then(() => {
          window.location.href = "tel:" + this.orderDetail.propertyPhoneNumber;
        });
      } else {
        window.location.href = "tel:" + this.orderDetail.propertyPhoneNumber;
      }
    }
  }
};
</script>

<style scoped>
